
import XHeader from 'vux/src/components/x-header/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import InlineXSwitch from 'vux/src/components/inline-x-switch/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import InlineCalendar from 'vux/src/components/inline-calendar/index.vue'


export default {
data () {
return {
msg1: '',
img1: require('../../assets/images/female_ic.png'),
img2: require('../../assets/images/male_ic.png'),
img3: require('../../assets/images/select_ic.png'),
img4: require('../../assets/images/unselect_ic.png'),
type: '',
newDate: '',
showBtn: false,
showBtn2: false,
showBtn3: false,
show: false,
value: '',
highlightWeekend: false,
return6Rows: false,
useCustomFn: false,
buildSlotFn: () => '',
disableFuture: true,
boxTil: '21天取水',
click1: true,
click2: true,
formData: {},
waterPurifier: {
analyseCompetitor: 0,
analyseCompetitorDate: '',
clientInfoId: 0,
clockInDate: [],
createBy: '',
createTime: '',
explainWaterCrisis: 0,
explainWaterCrisisDate: '',
giveDrink: 0,
giveDrinkDate: '',
giveWaterQualityDetector: 0,
giveWaterQualityDetectorDate: '',
id: '',
joinActivity: 0,
joinActivityDate: '',
phTest: 0,
phTestDate: '',
remark: '',
residualChlorine: 0,
residualChlorineDate: '',
returnWaterQualityDetector: 0,
returnWaterQualityDetectorDate: '',
twentyOneDayClock: 0,
type: '1',
updateBy: '',
updateTime: '',
waterPurifierTrialClock: 0,
waterPurifierTrialDate: []
},
hasFirstDishName: false,
hasSecondDishName: false,
pan: {
clientInfoId: 0,
comebackAgain: 0,
comebackAgainDate: '',
createBy: '',
createTime: '',
firstDishName: '',
giveCake: 0,
giveCakeDate: '',
id: '',
remark: '',
secondDishName: '',
type: '3',
updateBy: '',
updateTime: ''
},
sskit: {
clientInfoId: 0,
createBy: '',
createTime: '',
id: '',
recommendedAccount: 0,
recommendedAccountDate: '',
remark: '',
type: '',
updateBy: '',
updateTime: '',
watchFridge: 0,
watchFridgeDate: ''
},
twentyOneDayClock: 0,
waterPurifierTrialClock: 0,
inited: false,
foodTeachingList: [],
nutritionList: [],
nutrition_type: [],
slimBeautyList: [],
startDate: ''
}
},








watch: {
useCustomFn (val) {
this.buildSlotFn = val ? (line, index, data) => {
return /8/.test(data.date) ? '<div style="font-size:12px;text-align:center;"><span style="display:inline-block;width:5px;height:5px;background-color:red;border-radius:50%;"></span></div>' : '<div style="height:10px;"></div>'
} : () => ''
},
type (val) {
if (parseInt(val) === 2) {
this.slimBeautyList = []
} else if (parseInt(val) === 6) {
this.foodTeachingList = []
}
this._getInfo()
},
twentyOneDayClock (val) {
if (val) {
if (this.click1 && this.waterPurifier.clockInDate.length >= 21) {
this.$vux.toast.text('已打满21天')
this.twentyOneDayClock = 0
return
} else if (this.waterPurifier.clockInDate.indexOf(this.newDate) === -1) {
this.showBtn = true
this.waterPurifier.clockInDate.push(this.newDate)
}
} else {
this.$delete(this.waterPurifier.clockInDate, this.waterPurifier.clockInDate.indexOf(this.newDate))
}
this.click1 = true
this.showDakaDate(this.waterPurifier.clockInDate)
},
waterPurifierTrialClock (val) {
if (val) {
if (this.click2 && this.waterPurifier.waterPurifierTrialDate.length >= 7) {
this.$vux.toast.text('已打满7天')
this.waterPurifierTrialClock = 0
return
} else if (this.waterPurifier.waterPurifierTrialDate.indexOf(this.newDate) === -1) {
this.showBtn = true
this.waterPurifier.waterPurifierTrialDate.push(this.newDate)
}
} else {
this.$delete(this.waterPurifier.waterPurifierTrialDate, this.waterPurifier.waterPurifierTrialDate.indexOf(this.newDate))
}
this.click2 = true
this.showDakaDate(this.waterPurifier.waterPurifierTrialDate)
},
'waterPurifier': {
handler (val, oldVal) {
if (this.waterPurifier.explainWaterCrisis) {
this.waterPurifier.explainWaterCrisisDate = this.formData.waterPurifier && this.formData.waterPurifier.explainWaterCrisisDate ? this.formData.waterPurifier.explainWaterCrisisDate : this.newDate
} else {
this.waterPurifier.explainWaterCrisisDate = ''
}
if (this.waterPurifier.giveDrink) {
this.waterPurifier.giveDrinkDate = this.formData.waterPurifier && this.formData.waterPurifier.giveDrinkDate ? this.formData.waterPurifier.giveDrinkDate : this.newDate
} else {
this.waterPurifier.giveDrinkDate = ''
}
if (this.waterPurifier.residualChlorine) {
this.waterPurifier.residualChlorineDate = this.formData.waterPurifier && this.formData.waterPurifier.residualChlorineDate ? this.formData.waterPurifier.residualChlorineDate : this.newDate
} else {
this.waterPurifier.residualChlorineDate = ''
}
if (this.waterPurifier.phTest) {
this.waterPurifier.phTestDate = this.formData.waterPurifier && this.formData.waterPurifier.phTestDate ? this.formData.waterPurifier.phTestDate : this.newDate
} else {
this.waterPurifier.phTestDate = ''
}
if (this.waterPurifier.giveWaterQualityDetector) {
this.waterPurifier.giveWaterQualityDetectorDate = this.formData.waterPurifier && this.formData.waterPurifier.giveWaterQualityDetectorDate ? this.formData.waterPurifier.giveWaterQualityDetectorDate : this.newDate
} else {
this.waterPurifier.giveWaterQualityDetectorDate = ''
}
if (this.waterPurifier.returnWaterQualityDetector) {
this.waterPurifier.returnWaterQualityDetectorDate = this.formData.waterPurifier && this.formData.waterPurifier.returnWaterQualityDetectorDate ? this.formData.waterPurifier.returnWaterQualityDetectorDate : this.newDate
} else {
this.waterPurifier.returnWaterQualityDetectorDate = ''
}
if (this.waterPurifier.analyseCompetitor) {
this.waterPurifier.analyseCompetitorDate = this.formData.waterPurifier && this.formData.waterPurifier.analyseCompetitorDate ? this.formData.waterPurifier.analyseCompetitorDate : this.newDate
} else {
this.waterPurifier.analyseCompetitorDate = ''
}
if (this.waterPurifier.joinActivity) {
this.waterPurifier.joinActivityDate = this.formData.waterPurifier && this.formData.waterPurifier.joinActivityDate ? this.formData.waterPurifier.joinActivityDate : this.newDate
} else {
this.waterPurifier.joinActivityDate = ''
}
},
deep: true
},
'pan': {
handler (val, oldVal) {
if (this.pan.giveCake) {
this.pan.giveCakeDate = this.formData.pan && this.formData.pan.giveCakeDate ? this.formData.pan.giveCakeDate : this.newDate
} else {
this.pan.giveCakeDate = ''
}
if (this.pan.comebackAgain) {
this.pan.comebackAgainDate = this.formData.pan && this.formData.pan.comebackAgainDate ? this.formData.pan.comebackAgainDate : this.newDate
} else {
this.pan.comebackAgainDate = ''
}
},
deep: true
},
'sskit': {
handler (val, oldVal) {
if (this.sskit.watchFridge) {
this.sskit.watchFridgeDate = this.formData.sskit && this.formData.sskit.watchFridgeDate ? this.formData.sskit.watchFridgeDate : this.newDate
} else {
this.sskit.watchFridgeDate = ''
}
if (this.sskit.recommendedAccount) {
this.sskit.recommendedAccountDate = this.formData.sskit && this.formData.sskit.recommendedAccountDate ? this.formData.sskit.recommendedAccountDate : this.newDate
} else {
this.sskit.recommendedAccountDate = ''
}
},
deep: true
}
},
methods: {
callMobile (mobile) {
this.$util.callMobile(mobile)
},
_getDicts () {
let data = ['nutrition_type']
this.$tupHttp(this, 'front/dict/getDicts', 'POST', data).then(res => {
this.nutrition_type = res.data.model.nutrition_type
this.nutritionList.forEach((item, index) => {
if (index === 0) {
this.$set(item, 'isShow', true)
} else {
this.$set(item, 'isShow', false)
}
this.nutrition_type.forEach(item2 => {
if (parseInt(item2.dictValue) === parseInt(item.buyProduct)) {
item.buyProduct = item2.dictName
}
})
})
})
},
setArriveStore (index) {

if (this.inited) {
this.$vux.toast.text('拼命请求中，请稍候~')
return
}
let data = JSON.parse(JSON.stringify(this.foodTeachingList[index]))
data.arriveStore = this.foodTeachingList[index].arriveStore2 ? 1 : 0
delete data.arriveStore2
delete data.isShow
this.inited = true
this.$tupHttp(this, 'client-biz/client-biz/storeClient/arriveStore', 'POST', data).then(res => {
this.inited = false

this.$vux.toast.text('更新成功！')
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
setGetBack  (index) {

if (this.inited) {
this.$vux.toast.text('拼命请求中，请稍候~')
return
}
let data = JSON.parse(JSON.stringify(this.slimBeautyList[index]))
data.getBack = this.slimBeautyList[index].getBack2 ? 1 : 0
delete data.getBack2
delete data.isShow
this.inited = true
this.$tupHttp(this, 'client-biz/client-biz/storeClient/getBack', 'POST', data).then(res => {
this.inited = false

this.$vux.toast.text('更新成功！')
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
showDetail (index) {
this.foodTeachingList[index].isShow = !this.foodTeachingList[index].isShow
},
saveService (type) {
if (this.inited) {
this.$vux.toast.text('拼命请求中，请稍候~')
return
}
this.waterPurifier.clientInfoId = this.$route.query.id
let data = {
clientInfoId: this.$route.query.id,
type: type,
waterPurifier: type === 1 ? this.waterPurifier : null,
pan: type === 3 ? this.pan : null,
sskit: type === 4 ? this.sskit : null
}
this.inited = true
this.$tupHttp(this, 'client-biz/client-biz/storeClient/saveService', 'POST', data).then(res => {
this.inited = false
this.$vux.toast.text('保存成功！')
this._getInfo()
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
getDaKa () {
if (this.value === '') {
this.$vux.toast.text('请选择打卡日期')
return
}
this.showBtn = true
let dakaDate = []
if (this.boxTil === '21天取水') {
if (this.waterPurifier.clockInDate.length >= 21) {
this.$vux.toast.text('已打满21天')
return
}
for (let i = 0; i < this.waterPurifier.clockInDate.length; i++) {
if (this.waterPurifier.clockInDate[i] === this.value) {
this.$vux.toast.text('该日期已打卡，请重新选择')
return
}
}
if (this.value === this.newDate) {
this.twentyOneDayClock = 1
this.click1 = false
}
this.waterPurifier.clockInDate.push(this.value)
dakaDate = this.waterPurifier.clockInDate
} else {
if (this.waterPurifier.waterPurifierTrialDate.length >= 7) {
this.$vux.toast.text('已打满7天')
return
}
for (let i = 0; i < this.waterPurifier.waterPurifierTrialDate.length; i++) {
if (this.waterPurifier.waterPurifierTrialDate[i] === this.value) {
this.$vux.toast.text('该日期已打卡，请重新选择')
return
}
}
if (this.value === this.newDate) {
this.waterPurifierTrialClock = 1
this.click2 = false
}
this.waterPurifier.waterPurifierTrialDate.push(this.value)
dakaDate = this.waterPurifier.waterPurifierTrialDate
}
this.showDakaDate(dakaDate)
this.show = false
},
showDakaDate (dakaDate) {
this.buildSlotFn = (line, index, data) => {
let daka = dakaDate
let hasKa = false
daka.forEach(item => {
if (item === data.formatedDate) {
hasKa = true
}
})
return hasKa ? '<div style="font-size:12px;text-align:center;color:#ec6868;line-height:10px;"><i slot="icon" class="icon-select"></i></div>' : '<div style="height:2px;"></div>'
}
},
_getInfo () {
if (this.$route.query.id) {
this.$tupHttp(this, 'client-biz/client-biz/storeClient/clientDetail', 'POST', {clientInfoId: this.$route.query.id, type: this.type}).then(res => {
this.formData = res.data.model
this.formData.createTime = this.formData.createTime ? new Date(this.formData.createTime).format('yyyy-MM-dd hh:mm:ss') : this.formData.createTime
if (this.formData.waterPurifier) {
this.showBtn = false
this.waterPurifier = JSON.parse(JSON.stringify(this.formData.waterPurifier))
if (this.formData.waterPurifier.clockInDate.indexOf(this.newDate) !== -1) {
this.twentyOneDayClock = 1
this.click1 = false
}
if (this.formData.waterPurifier.waterPurifierTrialDate.indexOf(this.newDate) !== -1) {
this.waterPurifierTrialClock = 1
this.click2 = false
}
}
if (this.formData.foodTeachingList) {
this.foodTeachingList = JSON.parse(JSON.stringify(this.formData.foodTeachingList))
this.foodTeachingList.forEach((item, index) => {

this.$set(item, 'arriveStore2', !!item.arriveStore)
if (index === 0) {
this.$set(item, 'isShow', true)
} else {
this.$set(item, 'isShow', false)
}
})
}
if (this.formData.pan) {
this.showBtn2 = false
this.pan = JSON.parse(JSON.stringify(this.formData.pan))
if (this.formData.pan.firstDishName !== '') { this.hasFirstDishName = true }
if (this.formData.pan.secondDishName !== '') { this.hasSecondDishName = true }
}
if (this.formData.sskit) {
this.showBtn3 = false
this.sskit = JSON.parse(JSON.stringify(this.formData.sskit))
}
if (this.formData.nutritionList) {
this.nutritionList = JSON.parse(JSON.stringify(this.formData.nutritionList))
this._getDicts()
}
if (this.formData.slimBeautyList) {
this.slimBeautyList = JSON.parse(JSON.stringify(this.formData.slimBeautyList))
this.slimBeautyList.forEach((item, index) => {

this.$set(item, 'getBack2', !!item.getBack)
if (index === 0) {
this.$set(item, 'isShow', true)
} else {
this.$set(item, 'isShow', false)
}
})
}
})
}
},
toDetail () {
this.$router.push({
path: '/customerAdd',
query: {
id: this.$route.query.id
}
})
},
toFoodAdd () {
this.$router.push({
path: '/foodAdd',
query: {
id: this.$route.query.id
}
})
},
toNutritionAdd () {
this.$router.push({
path: '/nutritionAdd',
query: {
id: this.$route.query.id
}
})
},
toPotAdd () {
this.$router.push({
path: '/potAdd',
query: {
id: this.$route.query.id
}
})
},
showKa (til) {
this.boxTil = til
this.show = true
let dakaDate = []
if (this.boxTil === '21天取水') {
dakaDate = this.waterPurifier.clockInDate
} else {
dakaDate = this.waterPurifier.waterPurifierTrialDate
}
this.showDakaDate(dakaDate)
},
onChange (val) {

},
onViewChange (val, count) {

},
checkIcImg (name) {
if (this.waterPurifier.id && this.formData.waterPurifier[name]) { return }
this.showBtn = true
this.waterPurifier[name] = this.waterPurifier[name] ? 0 : 1
},
checkIcImg2 (name) {
if (this.pan.id && this.formData.pan[name]) { return }
this.showBtn2 = true
this.pan[name] = this.pan[name] ? 0 : 1
},
checkIcImg3 (name) {
if (this.sskit.id && this.formData.sskit[name]) { return }
this.showBtn3 = true
this.sskit[name] = this.sskit[name] ? 0 : 1
},
getTime () {
var _this = this
let yy = new Date().getFullYear()
let mm = new Date().getMonth() + 1 < 10 ? '0' + (new Date().getMonth() + 1) : new Date().getMonth() + 1
let dd = new Date().getDate() < 10 ? '0' + new Date().getDate() : new Date().getDate()
_this.newDate = yy + '-' + mm + '-' + dd
let startTime = new Date().setMonth((new Date().getMonth() - 2))
let yy2 = new Date(startTime).getFullYear()
let mm2 = new Date(startTime).getMonth() + 1 < 10 ? '0' + (new Date(startTime).getMonth() + 1) : new Date(startTime).getMonth() + 1
let dd2 = new Date(startTime).getDate() < 10 ? '0' + new Date(startTime).getDate() : new Date(startTime).getDate()
_this.startDate = yy2 + '-' + mm2 + '-' + dd2
}
},
created () {
this.getTime()
if (!this.$route.query.type) {
this._getInfo()
}
if (this.$route.query.type) {
this.type = this.$route.query.type.toString()
} else {
this.type = '1'
}
},
components: {
XHeader,
Checker,
CheckerItem,
XTextarea,
InlineXSwitch,
PopupHeader,
Popup,
InlineCalendar
}
}



